import React, {Component} from 'react';
import './blog.css';
import Helmet from 'react-helmet';
import picture from '../../Presentation/PageCover/picture.png';

export class Blog extends Component {

  renderText = () => {

      if(this.props.language === "portuguese") {
  
          return (
              <div className="container">
                <Helmet>
                  <meta
                    name="description"
                    content="Artigos sobre programação e outras coisas que me vierem à mente"
                  />
                  <title>Rodrigo Rocha Blog</title>
                  <meta http-equiv="content-language" content="pt-br"/>
                  <meta property="og:site_name" content="Rodrigo Rocha Blog"></meta>
                  <meta property="og:description" content="Artigos sobre programação e outras coisas que me vierem à mente" ></meta>
                  <meta property="og:title" content="Rodrigo Rocha Blog" ></meta>
                  <meta property="og:type"  content="website" ></meta>
                  <meta property="og:url"   content="https://rodrigogomesrc.github.io/blog"></meta>
                  <meta property="og:image" content={picture} ></meta>
                  <meta property="og:image:alt"    content="Foto do autor" ></meta>
                  <meta property="og:image:type"  content="image/png" ></meta>
                  <meta property="og:locale" content="pt_BR" ></meta>
                  <meta property="og:locale:alternate" content="pt_BR" ></meta>
                  <meta http-equiv="refresh" content="0; URL='https://rodrigorocha.space"/>
                </Helmet>
                <div>Redirecionando para o novo blog...</div>
            </div>
          )
  
      } else {
  
          return (
              <div className="container">
                <Helmet>
                  <meta
                    name="description"
                    content="Articles about programming and other things I may want to write"
                  />
                  <title>Rodrigo Rocha Blog</title>
                  <meta http-equiv="content-language" content="en-us"/>
                  <meta property="og:site_name" content="Rodrigo Rocha Blog"></meta>
                  <meta property="og:description" content="Articles about programming and other things I may want to write"></meta>
                  <meta property="og:title" content="Rodrigo Rocha Blog" ></meta>
                  <meta property="og:type"  content="website" ></meta>
                  <meta property="og:url"   content="https://rodrigogomesrc.github.io/en/blog"></meta>
                  <meta property="og:image" content={picture} ></meta>
                  <meta property="og:image:alt"    content="Author Picture" ></meta>
                  <meta property="og:image:type"  content="image/png" ></meta>
                  <meta property="og:locale" content="en_US" ></meta>
                  <meta property="og:locale:alternate" content="es_US" ></meta>
                  <meta http-equiv="refresh" content="0; URL='https://rodrigorocha.space/en"/>
                </Helmet>
                <div>Redirecting to the new blog...</div>
              </div>
          )
      }
  }
  
  render() {

      return (
          this.renderText()
      )
  }
}

export default Blog